.column {
	display: flex;
	box-sizing: border-box;
	.h2 {
		margin-bottom: 55px;
		color: #2d2d2d; }
	&-1 {
		width: 50%;
		padding-right: 140px;
		padding-top: 5px;
		box-sizing: border-box;
		ol {
			font-family: HelveticaNeue;
			line-height: 25px;
			list-style-type: decimal;
			list-style-position: inside; } }
	&-2 {
		width: 50%;
		padding-left: 83px;
		padding-top: 5px;
		box-sizing: border-box;
		border-left: 1px solid #a1907e; } }

